$(document).ready(function() {
  $(".nav>li").click(function() {
    var move = $(this).attr("data-index");
    var height = $(move).offset().top;
    $("html,body").stop().animate({ "scrollTop": height }, 1500);
  });

  $(".scroll-navigation").click(function() {
    var move = $(this).attr("data-index");
    var height = $(move).offset().top;
    $("html,nody").stop().animate({ "scrollTop": height }, 1500);
  });

  $(".button>li").first().click(function() {
    var learn_height = $("#Wrap-Money").offset().top;
    $("html,body").stop().animate({ "scrollTop": learn_height }, 1200);
  });

  $(".button>li").last().click(function() {
    var subs_height = $("#Subscribe").offset().top;
    $("html,body").stop().animate({ "scrollTop": subs_height }, 1200);
  });

  var left = -144;
  var auto2 = setInterval(function() {
    left = left - 2;
    $(".list-logo").css({ "left": left });
    if (left === -146) {
      $(".list-logo li").first().appendTo(".list-logo");
    }
    else if (left < -288) {
      left = -144;
    }
  }, 30);

  $("#videoSection").click(function() {
    $("iframe").fadeIn(500);
    $(".Background-black").show();
    $(".Close-video").show();
  });
  $(".Close-video").click(function() {
    $("iframe").hide();
    $(".Background-black").hide();
    $(this).hide();
  });

  $(".Token-Allo>ul>li").click(function() {
    $(".Token-Allo>ul>li").removeClass("on");
    $(this).addClass("on");
  });
  $(".Funds-Allo>ul>li").click(function() {
    $(".Funds-Allo>ul>li").removeClass("on");
    $(this).addClass("on");
  });

  $("#Team > .See-more-box").click(function() {
    $(this).parent("div").addClass("show");
    $("#Team>ul>li").show();
    $(this).parent("div").css({ "height": 1600 });
    $(this).hide();
  });

  $("#Advisors > .See-more-box").click(function() {
    $(this).parent("div").addClass("show");
    $(this).parent("div").css({ "height": 1100 });
    $("#Advisors>ul>li").show();
    $(this).hide();
  });

  $(".more").click(function() {
    $(this).siblings(".career").addClass("show");
  });
  $(".career").mouseleave(function() {
    $(this).removeClass("show");
  });

  $(".Tab-Menu>li").click(function() {
    $(".Tab-Menu>li").removeClass("on");
    $(this).addClass("on");
  });


  $(".Tab-Menu>li").click(function() {
    var list = $(this).index();

    $(".Tab").removeClass("show");
    $(".Tab").eq(list).addClass("show");
  });

  $(".Faq-Title>li").click(function() {
    $(this).siblings("li").removeClass("on");
    $(this).toggleClass("on");
    $(this).childeren("Faq-Content").stop().slideToggle();
  });
});
