$(document).ready(function() {
    var submit = $('#subscribe-submit');
    var email = $('#subscribe-email');

    submit.on('click', function(event) {
        if (email.val() !== '') {
            submit.val('Loading...');
            event.preventDefault();
            event.stopPropagation();

            $.ajax({
                type: "POST",
                url: 'https://script.google.com/macros/s/AKfycbxCyOFnA4wB8FqAmaCBMsyYDdhQAYZlIbGPe5lZY72FaF14B-c/exec',
                data: { email: email.val(), date: Date() },
                dataType: 'json'
            }).done(function(data) {

            }).always(function(data) {
                submit.val('Subscribed');
                // email.val('');
            });
        }
        else {
            email.focus();
        }
    });
});
